وبكل الوسائل، حتى لو كانت غير أخلاقية، يصبح متجردا من المشاعر ومن كل المبادىء الأخلاقية في سعيه، ولا يدرك أن سلوكه هذا قد يقوده إلى حتفه السريع. ‎بعد ان كان آلان مدير اعمال يسابق الزمن لا يوجد فى حياته مكان للترفيه وللعائلة. بعد اصابته بجلطة دماغيه يستيقظ آلان في المستشفى. حيث يتضح سريعا ً انه يعاني من فقدان كبير في اللغة والنطق. تم تعيين جين، وهي أخصائية علاج النطق الصغيرة، لمساعدته على إعادة التعلم، كما يتعلم الطفل، كيفية التحدث وكيفية إعادة توحيد نفسه. عندما يتعافى، بدأ يرى أنه ترك جزءًا كاملًا من حياته يفلت من أصابعه. لكنه يجد أيضًا أنه لم يضيع كل شيء، وأنه يمكنه تعويض الوقت الضائع، خاصة مع ابنته ")]):_vm._e(),_c('br'),_c('v-img',{attrs:{"max-width":"400","contain":"","src":require("../assets/samer-img/film-02.png")}}),_c('br'),_c('span',[_vm._v(" فيلم \"My beautiful stutter\" هو فيلم عن التلعثم والتلعثم يُشاهد من خلال جهود منظمة تحاول \"وضع الأطفال في مكان\" حيث يحبون أنفسهم، وتعليمهم \"التلعثم، ولا بأس بذلك\". ‎أسس تارو ألكساندر ، المتلعثم بنفسه ، المنظمة ، واكتشف في النهاية أن أفضل طريقة لمساعدة الأطفال على التأقلم هي إخبارهم بأنهم ليسوا وحدهم ‎ تدور احداث الفيلم حول خمسة أطفال يتلعثمون تتراوح اعمارهم بين 9 و18 عاماً من جميع انحاء الولايات المتحدة ومن جميع مناحي الحياة ، والذين ، بعد تجربة حياتهم من التنمر والوصم ، يلتقون بأطفال آخرين يتلعثمون في برنامج تفاعلي قائم على الفنون ، جمعية التلعثم للشباب ومقرها مدينة نيويورك. رحلتهم إلى SAY تجد البعض قريبًا من الانتحار، والبعض الآخر منسحب وخائف، مرهق ومهزوم من التدريب على الطلاقة الفاشلة، والضغوط المجتمعية لعدم التلعثم أو قرار التزام الصمت. على مدار عام، نشهد بشكل مباشر التحول المذهل الذي يحدث عندما يختبر هؤلاء الشباب من خلفيات مختلفة تمامًا لأول مرة الفكرة الثورية في قلب قول صاي: أنه لا بأس من التلعثم. ")]),_c('v-divider'),_c('br'),_c('v-img',{attrs:{"max-width":"400","contain":"","src":require("../assets/samer-img/film-03.png")}}),_c('br'),_c('span',[_vm._v(" هو فيلم فرنسي أخرجه جوليان شنابل وكتبه رونالد هاروود ورشح لجائزة الأوسكار للعام 2007. يستند الفيلم إلى مذكرات تحمل نفس الاسم كتبها الصحفي الفرنسي جان دومينيك بوبي. اب ‎تدور احداث الفيلم حول ان استيقظ جون دومينيك من غيوبة استمرت ل 20 يوماً متتالية ولكنه استيقظ انساناً مختلفاً ...! وزنة نقص قرابة ال 27 كيلو غرام واصيب بمرض نادر جداً يسمى بـ \"Locked-In syndrome \" \"متلازمة المنحبس\"، اثار المرض هي شلل كامل من تحت الرأس \"الرقبة\" الى أخمص القدم.! بل لم يتبقى له الى عينيه ليرمش بهم ومع تطور المرض يتم اقفال عينه اليمنى نهائيا حتى لا تصاب بتقرح لي ماضيه، الى رغباته، نزواته، علاقاته سواء مع عائلته او عشيقاته ومن ثم محور الفيلم وهي تلك الرواية المؤثرة جدا الذي كتبها شخص لا يقوى الى على اغماض عين، رواية اخذت من العمر سنتين ومن المجهود أكثر من 20 الفرمشة عين، بعد إطلاق الرواية ب 10 أيام وتحديدا في 9 مارس 1997 توفي جون دومينيك بوبي., الفيلم مؤثر وهادف ودرس عظيم في الحياة مع نهاية الفيلم نستنتج عظمة النعم التي أنعمها الله تعالى علينا ")]),_c('v-divider'),_c('br'),_c('v-img',{attrs:{"max-width":"400","contain":"","src":require("../assets/samer-img/film-04.png")}}),_c('br'),_c('span',[_vm._v(" تعديل ‎فيلم The Miracle Worker هو فيلم أمريكي عن السيرة الذاتية عام 1962عن آن سوليفان، المعلمة العمياء لهيلين كيلر، من إخراج آرثر بن تدور احداث الفيلم حول انه في ألاباما في ثمانينيات القرن التاسع عشر، أنجبت عائلة كيلر الثرية طفلة، هيلين كيلر. استدعى الكابتن آرثر كيلر وزوجته الثانية كيت كيلر طبيبًا لعلاج هيلين من الحمى. بعد فترة وجيزة، علموا أن هيلين فقدت قدرتها على الرؤية أو السمع. ‎مرت خمس سنوات ، وهيلين الآن فتاة صغيرة. لا يمكنها التواصل مع أي شخص ثم تتواصل عائله هيلين كيلر مع امرأة شابة تدعى آني سوليفان تصل آني إلى منزل كيلر، حيث تلتقي بهيلين. ولدهشة كيلر، تستخدم آني أسلوبًا عمليًا وعدوانيًا أكثر من أي من أسلافهامع مرور الأيام، تنجح آني في جعل هيلين تتعاون معها ")]),_c('v-divider'),_c('br'),_c('v-img',{attrs:{"max-width":"400","contain":"","src":require("../assets/samer-img/film-05.png")}}),_c('br'),_c('span',[_vm._v(" ‏\"Regarding Henry\" هو فيلم درامي أمريكي عام 1991 أخرجه مايك نيكولز وكتبه جي جي أبرامز. يقوم الفيلم ببطولة هاريسون فورد تدور احداث الفيلم حول صورة لمحامي مانهاتن كرجل مدفوع وطموح يعيش بثروة كبيرة وقليل من السعادة. ثم تحدث 