."),_c('br'),_c('br'),_c('div',{staticClass:"blue"},[_vm._v("ضخامة الرأس (Macrocephaly) "),_c('br')]),_vm._v(" يعاني بعض الأطفال المصابين بالتوحد لديهم من ضخامة الرأس، بينما مرضى تأخر النطق لا يعانون من هذا الأمر."),_c('br'),_c('br'),_c('br'),_c('div',{staticClass:"blue"},[_vm._v("اللغة التعبيرية"),_c('br')]),_vm._v(" يعاني الأطفال المصابون بالتوحد من صعوبات في التواصل الاجتماعي، على سبيل المثال: نقص التواصل البصري، ونقص استخدام الإشارة، بينما تكون هذه الصوبات أقل تواجدًا عند الأطفال الذين يعانون من تأخر النطق."),_c('br'),_c('br'),_c('br'),_c('div',{staticClass:"blue"},[_vm._v("المشكلات الجينية"),_c('br')]),_vm._v(" ارتبطت مجموعة من التشوهات الكروموسومية بمرض التوحد، بينما لا توجد صلة وثيقة بين تشوهات الكروموسومات وتأخر النطق."),_c('br'),_c('br'),_c('br'),_c('br'),_c('strong',[_c('div',{staticClass:"grey"},[_vm._v(" أهم النقاط المشتركة بين التوحد وتأخر النطق"),_c('br')])]),_vm._v(" على الرغم من الفرق بين التوحد وتأخر النطق، إلا أن هناك مجموعة من الخصائص المشتركة بين التوحد وتأخر النطق، ومنها ما يأتي: "),_c('br'),_c('br'),_vm._v(" سن ظهور الأعراض: يبدأ ظهور أعراض التوحد وتأخر النطق في السنة الثانية والثالثة تقريبًا."),_c('br'),_vm._v(" مشكلات النطق: يعاني كل من مرضى التوحد وتأخر النطق من التأخر في النطق."),_c('br'),_vm._v(" المهارات الاجتماعية: يعاني كل من مرضى التوحد وتأخر النطق من ضعف في التواصل الاجتماعي وتكوين العلاقات."),_c('br'),_vm._v(" عوامل الخطورة: يشترك كل من مرضى التوحد وتأخر النطق بمجموعة من عوامل الخطورة، أهمها: الجنس الذكري، والولادة المبكرة، والتاريخ العائلي بهذه الأمراض."),_c('br'),_c('br'),_c('strong',[_c('div',{staticClass:"grey"},[_vm._v("علاج التوحد وتأخر النطق"),_c('br')])]),_vm._v(" هناك طرق مختلفة لعلاج التوحد وتأخر النطق، إذ يتم التعامل مع مرض التوحد باستخدام العلاج السلوكي والمعرفي (Cognitive behavioral therapy) والدعم الأسري والمعنوي. "),_c('br'),_c('br'),_vm._v(" أما بالنسبة لتأخر النطق فبعد التشخيص الأولي للمريض، يتم إجراء اختبار السمع للتأكد من أن فقدان السمع أو الصمم ليس سببًا أساسيًا لتأخر النطق، في حين أن تقويم النطق (Speech therapy) هو أكثر العلاجات شيوعًا لتأخر النطق، فقد يستفيد الأطفال من العلاجات المهنية والبدنية أيضًا."),_c('br'),_c('br'),_c('br'),_c('strong',[_c('div',{staticClass:"grey"},[_vm._v("متى يتكلم الطفل التوحدي؟ "),_c('br')])]),_vm._v(" ويعاني كثير من الأطفال المصابين بالتوحد من بعض الصعوبات في التواصل والكلام، تتراوح بين البسيطة والشديدة التي قد تصل إلى ألا يتحدث الطفل التوحدي على الإطلاق. وتشير الإحصاءات إلى أن 40% من الأطفال المصابين بالتوحد لا يستطيعون الكلام أو يتحدثون بكلمات قليلة لا تتعدى أصابع اليد طوال حياتهم، وهو أمر قد يقلق الأمهات، ما يدفعهن للتساؤل: متى يتكلم الطفل التوحدي؟ وكيف يمكن تطوير مهارات التواصل والحديث لديه؟ وسنجيبكِ عزيزتي عن كل هذه التساؤلات في السطور التالية. متى يتكلم الطفل التوحدي؟ كما ذكرنا، فإن غالبية الأطفال المصابين بالتوحد تكون لديهم مشكلات في التواصل والتحدث، أو لا يتحدثون على الإطلاق، وتتطور المهارات اللغوية لديهم في وقتٍ متأخر، مقارنةً بالطفل العادي، ففي الوقت الذي ينطق الطفل الطبيعي كلماته الأولى في عمر 12- 18 شهرًا ، فإن الطفل التوحدي قد ينطق أولى كلماته في عمر 36 شهرًا، ومع ذلك لأن طبيعة كل طفل تختلف عن الآخر، فإن العمر الذي يبدأ فيه الطفل التوحدي الكلام يختلف حسب تطور الحالة. ولسنوات طويلة كان هناك معتقد شائع أن الأطفال المصابين بالتوحد إذا لم يتحدثوا بحلول عامهم الرابع فإنهم لن يتحدثوا أبدًا، ومع ذلك، أظهرت دراسة حديثة أُجريت على عدد من الأطفال المصابين بالتوحد، أن معظم الأطفال المشاركين في الدراسة اكتسبوا مهارات لغوية، وأصبح نصفهم تقريبًا من المتحدثين بطلاقة، وأن أكثر من 70% منهم أصبحوا قادرين على نطق عبارات بسيطة رغم أن معظمهم لم يتحدث إلا بعد سن الرابعة، ما يعني أن الأطفال المصابين بالتوحد المتأخرين في الكلام، يمكن أن تتطور لديهم المهارات اللغوية مع مساعدتهم من خلال جلسات التخاطب والطرق التفاعلية الأخرى"),_c('br'),_c('br'),_c('strong',[_c('div',{staticClass:"grey"},[_vm._v(". إصدار الأصوات عند أطفال التوحد"),_c('br')])]),_vm._v(" كما ذكرنا فإن نحو 40% من الأطفال المصابين بالتوحد لا يتحدثون على الإطلاق، لكن عادةً ما تكون مهارات اللغة والتواصل لديهم محدودة، وفي الطبيعي فإن الأطفال يصدرون أصواتًا كمؤشر للكلام، فيفتح الطفل فمه ويغلقه ويحرك لسانه في محاولة منه لتقليد البالغين، فيصدر أصواتًا أقرب لثرثرة غير مفهومة، وفي كثير من الأحيان، قد يُصدر الأطفال المصابون بالتوحد أيضًا أصواتًا لا معنى لها تُعد لغتهم الخاصة، وهي أقرب لسلوك تحفيزي يحاول به الطفل التوحدي التعبير، ولكن لا يستخدمها في التواصل مع الآخرين. وتشير الدراسات إلى أن معدل الثرثرة أو معدل إصدار الأصوات عند الأطفال المصابين بالتوحد غير اللفظي (الذين لا يتحدثون أو يواجهون مشكلات شديدة في التكلم) يكون أقل منه لدى أقرانهم الذين يستطيعون التحدث، ومع ذلك، فإن تلك الأصوات تُعد مؤشرًا جيدًا أن طفلك يحاول التحدث والنطق، وتؤكد الدراسات أن غالبية الأطفال المصابين بالتوحد يمكن أن يطوروا اللغة بشكل أسرع إذا كان لديهم مشاركة وتفاعل اجتماعي قوي من المحيطين، ولا يعانون من أي إعاقة ذهنية. أما إذا كان الطفل لا يصدر أصواتًا وتأكد تشخيصه بالتوحد غير اللفظي، فيمكن استخدام التواصل البصري والإيماءات وتطويرها كأداة للتواصل معه."),_c('br'),_c('br'),_c('br'),_c('strong',[_c('div',{staticClass:"grey"},[_vm._v("كيف تجعل طفل التوحد يتكلم؟ "),_c('br')])]),_vm._v(" رغم عدم وجود علاج لمرض التوحد، فإن هناك علاجات قد تساعد الطفل على التواصل، وتحفز بعض مهارات اللغة لديه، ولكن يجب أن تعلمي جيدًا عزيزتي أن كل طفل حالة فريدة من نوعه، وأن الأسلوب الذي قد يجدي نفعًا مع طفل، قد لا يناسب طفلًا آخر، وفي ما يلي بعض النصائح العامة التي قد تساعد الطفل المصاب بالتوحد على الحديث: تحفيز الطفل على تقليدك: الأصوات التي قد يصدرها الطفل أمامك بعد سماعك تتحدثين محاولة منه لتقليدك، فالتقليد الوسيلة الأولى التي يمكن بها مساعدة الطفل على تطوير مهاراته اللغوية، في البداية حاولي أن يقلدك الطفل في الحركات مثل التصفيق، وتربيت الرأس، أو أن يقبّلك وما إلى ذلك، وقد يستغرق الأمر منكِ ومنه وقتًا، لكنه في الغالب سيستجيب لكِ، واحرصي على تشجيعه دائمًا عندما ينجح في تقليدك، ثم ابدئي استخدام كلمات بسيطة، وتكرارها أمامه بعد أن يعتاد تقليد حركاتك. الغناء للطفل: غني لطفلك، فالغناء من الأشياء التي يستجيب لها الأطفال بصفة عالفرق بين التوحد وتأخر الكلام"),_c('br'),_c('br'),_vm._v(" والطفل التوحدي على وجه الخصوص، ويُفضل غناء أغاني بسيطة، مثل ABC، أو التي الأغاني التي تحتوي على أصوات أو مقاطع مميزة وسهلة مثل Baby Shark، وحاولي أن تتوقفي عن الغناء عند تلك الأصوات، لإعطاء الطفل فرصة أن يكمل الكلمة أو الصوت الناقص في الأغنية مثل أن تقولى بيبي شارك ثم تصمتي، ليجيبك بالمقطع \"دو دو دو\"، وحتى لو لم ينجح الصغير في إصدار نفس الصوت وأصدر صوتًا آخر فهذا يُعد تطورًا كبيرًا، فهو يعرف الآن أن هناك كلمة ناقصة ويحاول التفاعل معكِ وإكمال الأغنية. استخدام الأشياء التي يحبها: عادةً ما يرتبط الطفل التوحدي بلعبة معينة أو غرض معين لا يفارقه، استغلي هذا الأمر في أن تعززي مهارات التواصل لدى الصغير، فعندما يشير إلى لعبته المفضلة، فاسأليه: ماذا تريد؟ وانتظري قليلًا، فإذا أشار إليها وأصدر صوتًا، أعطها له حتى لو لم يكن الصوت قريبًا من الكلمة، ولكن في كل مرة حاولي دفعه وتشجيعه قليلًا، حتى ينجح في أن ينطق الكلمة المعبرة عن لعبته المفضلة. التفاعل مع الطفل: كلما تحدثتِ مع الطفل أنتِ وباقي أفراد الأسرة ستشجعينه على التجاوب والتفاعل، حاولي الحد من وقت استخدام الهاتف والتلفاز وألعاب الفيديو، والتي قد ينجذب لها الطفل التوحدي بشكل أكبر من الطفل العادي، هذه الأشياء لا تسمح للطفل بالتفاعل، وتزيد من سوء الحالة، لأنه يجلس فقط ويراقب فيصبح مستقبلًا للمعلومات، أما حين يلعب معكِ ومع إخوته، فهو يتفاعل ويستجيب، ما يعزز مهارات التحدث والتواصل والمهارات الاجتماعية أيضًا لديه. "),_c('br'),_vm._v(" جلسات التخاطب: وتُعد من أهم الخطوات التي يجب اتخاذها مع الطفل التوحدي، إذ يستجيب بشكل جيد للجلسات المنظمة، ويمكن لها أن تساعده على تطوير مهارات التواصل، لذا بمجرد شعورك أن طفلك يبدي بعض علامات التوحد، استشيري طبيبًا متخصصًا، وسيحدد لكِ جلسات، تساعد على تطوير مهارات الطفل في وقتٍ مبكر، ما يزيد من فرص تطور مهاراته اللغوية لاحقًا"),_c('br'),_c('br'),_c('br'),_c('strong',[_c('div',{staticClass:"grey"},[_vm._v("دراسة اميرة محمد البيطار"),_c('br')])]),_vm._v(" الفرق بين التوحد وتأخر الكلام كلاهما لديه المواهب والقدرات وعلينا معرفة ذلك الفرق حتى نصعد بهم لعنان السماء."),_c('br'),_c('br'),_vm._v(" فلقد خلقنا الله مختلفين ليميزنا وليكون لكل واحد منا سمة وبصمة في عمله وحياته يتميز بها. وهنا سأعرض أوجه الاختلاف بين الاثنين حتى يتثنى للأبوين معرفة طبيعة ابنهما."),_c('br'),_vm._v(" الطفل الذي يعاني من تأخر الكلام تجده يتواصل مع الآخرين بالرغم من عد